@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Mono|Roboto+Condensed);
::-moz-selection {
  background-color: rgb(196, 241, 255);
  color: black;
}

::selection {
  background-color: rgb(196, 241, 255);
  color: black;
}


body {
  font-family: Roboto;
  line-height: 155%;
  background: rgb(27, 26, 37);
  color: rgba(255, 255, 255, 0.849);
}

textarea {
  font-family: Roboto;
  background: rgb(20, 36, 55);
  border: 1px solid rgb(108, 113, 133);
  color: white;
  padding: 15px;
  height: 700px;
  width: 400px;
  margin: 0 30px 0 0;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}

a {
  color: rgb(88, 148, 226);
}

a:hover {
   color: rgb(178, 207, 245);
}

#display {
  width: 400px;
  height: 700px;
  padding: 15px;
  background-color: rgb(28, 32, 47);
  border: 1px solid grey;
  overflow-y: auto;
}

.Markdown {
  display: -webkit-flex;
  display: flex;
}

@media (max-width: 700px) {
  .Markdown {
    -webkit-flex-flow: column;
            flex-flow: column;
  }
  textarea {
      margin-bottom: 50px;
  }
}

#Title {
  font-family: "Roboto Condensed";
  color: rgb(164, 197, 215);  
  font-size: 3rem;
  margin: 30px 0 0 0;
}

#Subtitle {
  font-family: "Roboto Condensed";
  margin-bottom: 50px;
  color: rgb(75, 149, 189);  
}
code {
  font-family: "Roboto Mono";
  color: white;
  background-color: rgb(45, 51, 53);
  padding: 3px;
  white-space: pre-wrap;
}

li {
  list-style-type:  circle;
}
